@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@import url(https://fonts.googleapis.com/css?family=Walter+Turncoat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Atomic+Age&display=swap);
@import url(https://fonts.googleapis.com/css?family=Squada+One&display=swap);
.version-holder {
  position: absolute;
  bottom: 0;
  right: 0;

  text-align: right;

  color: white;
}

.input {
  --color-default: white;
  --color-blur: rgba(255, 255, 255, 0.6);
  --color-focus: #f1c857;
  --color-error: #ff4f4f;

  width: 280px;
}

.input-body {
  height: 56px;

  background: rgba(0, 0, 0, 0.2);
  border-radius: 5px 5px 0px 0px;
}

.input-icon {
  color: var(--color-blur);
}

.error-icon {
  color: var(--color-error);
}

.input hr {
  border: none;
  height: 2px;

  background-color: var(--color-blur);
}

.input-label {
  position: absolute;
  font-size: 12px;
  line-height: 16px;

  display: flex;
  align-items: flex-end;
  text-transform: capitalize;

  color: var(--color-default);
}

.input-value {
  font-size: 16px;
  color: var(--color-default);
  border: none;
  background: none;
  outline: none;
  padding: 0;
  height: 20px;
}

.input-footer {
  height: 20px;
  font-size: 12px;
  min-width: 0;
}

.helper-text {
  color: var(--color-blur);
}

.error-text {
  color: var(--color-error);
}

/* BLUR */
.input .input-label {
  color: var(--color-blur);
}

.input hr,
.input-footer {
  -webkit-transition: 150ms ease-in-out all;
  transition: 150ms ease-in-out all;
}

/* EMPTY */
.input .input-label {
  -webkit-transform: translate(0, -100%);
          transform: translate(0, -100%);
  font-size: 12px;
  -webkit-transition: 150ms ease-in-out all;
  transition: 150ms ease-in-out all;
}

.input.is-empty:not(.is-focused) .input-label {
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  font-size: 16px;
}
/* FOCUS */

.input.is-focused hr {
  background-color: var(--color-focus);
}

.input.is-focused .input-label {
  color: var(--color-focus);
}

/* ERROR */

.input.has-error .input-label {
  color: var(--color-error);
}

.input.has-error hr {
  background-color: var(--color-error);
}

.status-holder {
  position: absolute;
  top: 0;
  right: 0;

  color: white;
}

.rooms.form {
  max-height: 80vh;
  overflow: hidden;
}

.player-list,
.room-list {
  width: 320px;
}

/* todo(vmyshko): provide better naming, move to flex.css */
.fill-left {
  flex: 1 1 auto;
  text-align: left;
}

.app {
  text-align: center;

  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-image: url(/pg-web/static/media/pg-background.b5f18203.png);

  -webkit-transition: all ease-in-out 300ms;

  transition: all ease-in-out 300ms;

  background-color: #696969;
}

.app.is-online {
  background-color: #778b44;
}

.app-logo {
  height: 40vmin;
  pointer-events: none;
}

.is-pulse {
  -webkit-animation: pulse infinite 1s ease-in-out alternate;
          animation: pulse infinite 1s ease-in-out alternate;
}

@-webkit-keyframes pulse {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0.8;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes pulse {
  from {
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    opacity: 0.8;
  }
  to {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.city-card {
  position: absolute;
  background-color: #b0afaa; /* city-card/bg */
  border-radius: 10px;
  /* border: inset 2px solid var(--color-text-b2); */

  box-shadow: var(--shadow-border-b1), var(--shadow-1);
}

.city-name {
  font-size: 20px;
  font-family: var(--font-caption);
  text-transform: uppercase;
  color: white;

  border: 1px solid black;
  box-shadow: var(--shadow-1);
  border-radius: 2px;
}

.city-slot:nth-child(1) {
  --color: #63a36f;
}

.city-slot:nth-child(2) {
  --color: #a1a294;
}

.city-slot:nth-child(3) {
  --color: #8fa2a8;
}

.city-slot {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 60px;
  width: 50px;

  color: white;
  border-radius: 3px;
  background-color: var(--color);
  box-shadow: var(--shadow-border-b3);

  font-family: var(--font-number);

  cursor: pointer;
}

.city-slot:hover {
