}

.map-overlay {
  position: fixed;
  display: block;
  /* overflow: scroll; */
  overflow: hidden;

  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;

  /* box-shadow: inset 0 0 0 3px red; */
}

.map-content {
  position: absolute;
  -webkit-transform-origin: top left;
          transform-origin: top left;
  /* transform-origin: center center; */
  background-color: rgba(0, 255, 98, 0.123);
  box-shadow: inset 0 0 0 10px rgb(0, 255, 42);
}

.map-bg {
  background-image: url(/pg-web/static/media/map-cities-contrast.bdf23584.png),
    url(/pg-web/static/media/map-scroller-raw-wavy.5641dea6.png);
