.button {
  flex: 0 0 auto;
  height: 50px;

  font-size: 14px;
  padding: 0 var(--num3);

  box-sizing: border-box;
  border: 2px solid rgba(0, 0, 0, 0.7);
  border-radius: 5px;
  /* todo: extract to var? */

  background-color: #bdb58e;
  color: var(--color-text-b1);
  box-shadow: var(--shadow-0);

  text-transform: capitalize;
  cursor: pointer;
  outline: none;
}

.button:focus:not([disabled]) {
  outline: gold;
}
.button:hover:not([disabled]) {
  background-color: #d4cda4;
  box-shadow: var(--shadow-1);
}

.button:active:not([disabled]) {
  background-color: #9b9473;
  box-shadow: var(--shadow-0);
}

.button[disabled] {
  color: inherit;
  cursor: not-allowed;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}

