.city-card {
  position: absolute;
  background-color: #b0afaa; /* city-card/bg */
  border-radius: 10px;
  /* border: inset 2px solid var(--color-text-b2); */

  box-shadow: var(--shadow-border-b1), var(--shadow-1);
}

.city-name {
  font-size: 20px;
  font-family: var(--font-caption);
  text-transform: uppercase;
  color: white;

  border: 1px solid black;
  box-shadow: var(--shadow-1);
  border-radius: 2px;
}

.city-slot:nth-child(1) {
  --color: #63a36f;
}

.city-slot:nth-child(2) {
  --color: #a1a294;
}

.city-slot:nth-child(3) {
  --color: #8fa2a8;
}

.city-slot {
  display: flex;
  align-items: center;
  justify-content: center;

  height: 60px;
  width: 50px;

  color: white;
  border-radius: 3px;
  background-color: var(--color);
  box-shadow: var(--shadow-border-b3);

  font-family: var(--font-number);

  cursor: pointer;
}

.city-slot:hover {
  box-shadow: var(--shadow-border-b2);
}

.region-red {
  background-color: #e36860;
}
.region-teal {
  background-color: rgb(56, 170, 182);
}
.region-brown {
  background-color: #b07f69;
}
.region-yellow {
  background-color: #e8ce66;
}
.region-green {
  background-color: #5db541;
}
.region-violet {
  background-color: rgb(166, 93, 233);
}
/* from SAR */
.region-orange {
  background-color: #e5ad3e;
}
